import { reactive } from 'vue'

class MetadataModel {
  constructor() {
    if (MetadataModel.instance) {
      return MetadataModel.instance;
    }

    /**
     * ÈáçÁÇπÔºö‰ΩøÁî® Vue.observable() ÂåÖË£Ö data
     * ‰ΩøÂæóÊ≠§ÂØπË±°ÂÖ∑Â§á Vue ÁöÑÂìçÂ∫îÂºèÊ£ÄÊµãËÉΩÂäõ
     */
    this.data = reactive({
      education: [],
      workExperience: [],
      projectExperience: [],
      personalInfo: {},
      personalSummary: ''
    });

    MetadataModel.instance = this;
  }

  metaDataDescribeForType(type) {
    switch (type) {
      case 'education':
        return `
        - meta_data ‰∏≠ÁöÑ title ÊòØÁî®Êà∑ÁöÑÊïôËÇ≤ÁªèÂéÜÊ†áÈ¢òÔºå ‰∏çÂÅöÊîπÂä®„ÄÇ
        - meta_data ‰∏≠ÁöÑ content ÊòØAIÈúÄË¶ÅÈÄöËøáÂØπËØùÔºåÊÄªÁªìÂá∫ÁöÑÊïôËÇ≤ÁªèÂéÜÂÜÖÂÆπÔºà‰∫ÆÁÇπÔºâÔºåÈúÄÊÄªÁªì‰∏∫2-4ÁÇπÔºåÁ™ÅÂá∫Â≠¶‰π†ÊàêÊûúÂíåÁõ∏ÂÖ≥ËØæÁ®ã, bullet_point ÊòØÊÄªÁªìÂ∞èÊ†áÈ¢òÔºåcontent ÊòØÊÄªÁªìÂÜÖÂÆπ
        - meta_data ‰∏≠ÁöÑ from_time ÊòØÁî®Êà∑ÁöÑÂÖ•Â≠¶Êó∂Èó¥„ÄÇ
        - meta_data ‰∏≠ÁöÑ to_time ÊòØÁî®Êà∑ÁöÑÊØï‰∏öÊó∂Èó¥„ÄÇ
        `;
      case 'workExperience':
        return `
        - meta_data ‰∏≠ÁöÑ title ÊòØÁî®Êà∑ÁöÑÂ∑•‰ΩúÁªèÂéÜÊ†áÈ¢ò, ‰∏çÂÅöÊîπÂä®„ÄÇ
        - meta_data ‰∏≠ÁöÑ content ÊòØAIÈúÄË¶ÅÈÄöËøáÂØπËØùÔºåÊÄªÁªìÂá∫ÁöÑÂ∑•‰ΩúÁªèÂéÜÂÜÖÂÆπÔºà‰∫ÆÁÇπÔºâÔºåÈúÄÊÄªÁªì‰∏∫2-4ÁÇπÔºåÂº∫Ë∞ÉËÅåË¥£„ÄÅÊàêÂ∞±ÂíåÈáèÂåñÊàêÊûú, bullet_point ÊòØÊÄªÁªìÂ∞èÊ†áÈ¢òÔºåcontent ÊòØÊÄªÁªìÂÜÖÂÆπ
        - meta_data ‰∏≠ÁöÑ from_time ÊòØÁî®Êà∑ÁöÑÂÖ•ËÅåÊó∂Èó¥„ÄÇ
        - meta_data ‰∏≠ÁöÑ to_time ÊòØÁî®Êà∑ÁöÑÁ¶ªËÅåÊó∂Èó¥„ÄÇ
        `;
      case 'projectExperience':
        return `
        - meta_data ‰∏≠ÁöÑ title ÊòØÁî®Êà∑ÁöÑÈ°πÁõÆÁªèÂéÜÊ†áÈ¢òÔºå‰∏çÂÅöÊîπÂä®„ÄÇ
        - meta_data ‰∏≠ÁöÑ content ÊòØAIÈúÄË¶ÅÈÄöËøáÂØπËØùÔºåÊÄªÁªìÂá∫ÁöÑÈ°πÁõÆÁªèÂéÜÂÜÖÂÆπÔºà‰∫ÆÁÇπÔºâÔºåÈúÄÊÄªÁªì‰∏∫2-4ÁÇπÔºåÁ™ÅÂá∫È°πÁõÆÁõÆÊ†á„ÄÅÊâÄÁî®ÊäÄÊúØÂíåÂèñÂæóÁöÑÊàêÊûú, bullet_point ÊòØÊÄªÁªìÂ∞èÊ†áÈ¢òÔºåcontent ÊòØÊÄªÁªìÂÜÖÂÆπ
        - meta_data ‰∏≠ÁöÑ from_time ÊòØÈ°πÁõÆÂºÄÂßãÊó∂Èó¥„ÄÇ
        - meta_data ‰∏≠ÁöÑ to_time ÊòØÈ°πÁõÆÁªìÊùüÊó∂Èó¥„ÄÇ
        `;
      case 'personalInfo':
        return `
        - meta_data ‰∏≠ÁöÑ name ÊòØÁî®Êà∑ÂßìÂêçÔºå‰Ω†ÈúÄË¶ÅÊ†πÊçÆÁî®Êà∑ÁöÑÂØπËØùÔºåÊÄªÁªìÂá∫nameÁöÑÂÜÖÂÆπÔºåÊòØÁî®Êà∑ÁöÑÂßìÂêç„ÄÇ
        - meta_data ‰∏≠ÁöÑ email ÊòØÁî®Êà∑ÈÇÆÁÆ±Ôºå‰Ω†ÈúÄË¶ÅÊ†πÊçÆÁî®Êà∑ÁöÑÂØπËØùÔºåÊÄªÁªìÂá∫emailÁöÑÂÜÖÂÆπÔºåÊòØÁî®Êà∑ÁöÑÈÇÆÁÆ±„ÄÇ
        - meta_data ‰∏≠ÁöÑ phone ÊòØÁî®Êà∑ÁöÑËÅîÁ≥ªÁîµËØùÔºå‰Ω†ÈúÄË¶ÅÊ†πÊçÆÁî®Êà∑ÁöÑÂØπËØùÔºåÊÄªÁªìÂá∫phoneÁöÑÂÜÖÂÆπÔºåÊòØÁî®Êà∑ÁöÑËÅîÁ≥ªÁîµËØù„ÄÇ
        `;
      case 'personalSummary':
        return `
        - meta_data ‰∏≠ÁöÑ content ÊòØÁî®Êà∑Â°´ÂÜôÁöÑ‰∏™‰∫∫ÊÄªÁªìÔºå‰Ω†ÈúÄË¶ÅÊ†πÊçÆÁî®Êà∑ÁöÑÂØπËØùÔºåÊÄªÁªìÂá∫contentÁöÑÂÜÖÂÆπÔºåÊòØÁî®Êà∑ÁöÑ‰∏™‰∫∫ÊÄªÁªì„ÄÇ
        - meta_data ‰∏≠ÁöÑ skills ÊòØÁî®Êà∑ÁöÑÊäÄËÉΩÂàóË°®Ôºå‰Ω†ÈúÄË¶ÅÊ†πÊçÆÁî®Êà∑ÁöÑÂØπËØùÔºåÊÄªÁªìÂá∫skillsÁöÑÂÜÖÂÆπÔºåÊòØÁî®Êà∑ÁöÑÊäÄËÉΩ„ÄÇ
        `;
      default:
        return '';
    }
  }

  // ËøîÂõû‰∏çÂêåtypeÁöÑÊï∞ÊçÆÁªÑÁªáÊ†ºÂºèÔºåÁïôÁªôÁî®Êà∑Â°´ÂÖÖ
  formatForType(type) {
    switch (type) {
      case 'education':
      case 'workExperience':
      case 'projectExperience':
        return JSON.stringify({
          "title": "",
          "from_time": "",
          "to_time": "",
          "content": [
            {
              "bullet_point": "ÊÄªÁªìÂ∞èÊ†áÈ¢ò1",
              "content": "ÊÄªÁªìÂÜÖÂÆπ1"
            },
            {
              "bullet_point": "ÊÄªÁªìÂ∞èÊ†áÈ¢ò2",
              "content": "ÊÄªÁªìÂÜÖÂÆπ2"
            },
            {
              "bullet_point": "ÊÄªÁªìÂ∞èÊ†áÈ¢ò3",
              "content": "ÊÄªÁªìÂÜÖÂÆπ3"
            },
          ],
        });
      case 'personalInfo':
        return JSON.stringify({
          "name": "",
          "email": "",
          "phone": "",
        });
      case 'personalSummary':
      default:
        return '{}';
    }
  }

  /**
   * Ê†πÊçÆ type Ëé∑ÂèñÊï∞ÊçÆ
   * Ê≥®ÊÑèÔºöÁé∞Âú®Ë¶ÅËÆøÈóÆÊó∂ÔºåÁî® this.data[type]
   */
  contentForType(type, title = null) {
    switch (type) {
      case 'education':
        return title
          ? this.data.education.find(item => item.title === title)
          : this.data.education;
      case 'workExperience':
        return title
          ? this.data.workExperience.find(item => item.title === title)
          : this.data.workExperience;
      case 'projectExperience':
        return title
          ? this.data.projectExperience.find(item => item.title === title)
          : this.data.projectExperience;
      case 'personalInfo':
        return this.data.personalInfo;
      case 'personalSummary':
        return this.data.personalSummary;
      default:
        return null;
    }
  }

  /**
   * setContentForType
   * ÔºàÁ§∫‰æãÂè™ÊºîÁ§∫‰∏Ä‰∏ãÂÜÖÈÉ®ËÆøÈóÆÊñπÂºèÔºåÈÄªËæëÂíå‰Ω†ÂéüÊù•Á±ª‰ººÔºâ
   */
  setContentForType(type, content, title = null) {
    switch (type) {
      case 'education': {
        if (title) {
          const existing = this.data.education.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.education.push({ title, content });
          }
        } else {
          this.data.education.push({ content });
        }
        break;
      }
      case 'workExperience': {
        if (title) {
          const existing = this.data.workExperience.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.workExperience.push({ title, content });
          }
        } else {
          this.data.workExperience.push({ content });
        }
        break;
      }
      case 'projectExperience': {
        if (title) {
          const existing = this.data.projectExperience.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.projectExperience.push({ title, content });
          }
        } else {
          this.data.projectExperience.push({ content });
        }
        break;
      }
      case 'personalInfo':
        this.data.personalInfo = content;
        break;
      case 'personalSummary':
        this.data.personalSummary = content;
        break;
      default:
        break;
    }
  }


  /**
   * setContentForTitle
   * Ê†πÊçÆÊ†áÈ¢òÊõ¥Êñ∞ÊüêÈ°πÁöÑ content
   */
  setContentForTitle(title, content) {
    const types = ['education', 'workExperience', 'projectExperience'];
    for (const type of types) {
      const arr = this.data[type];
      if (!arr || !Array.isArray(arr)) continue;
      // Êü•ÊâæÁ¨¶Âêà title ÁöÑÈ°πÂπ∂Êõ¥Êñ∞
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].title === title) {
          arr[i].content = content;
          return;
        }
      }
    }
  }

}

// Âè™ÂÆû‰æãÂåñ‰∏ÄÊ¨°
const metadataInstance = new MetadataModel();
Object.freeze(metadataInstance); // Èò≤Ê≠¢‰øÆÊîπÂÆû‰æã

export default metadataInstance
