<script>
import metadataInstance from '@/models/metadata_model.js' // ËøôË°åÊòØ‰Ω†ÈúÄË¶ÅÊñ∞Â¢ûÁöÑÂØºÂÖ•

export default {
  name: 'ResumeForm',
  methods: {
    // ========= Êèê‰∫§ÊåâÈíÆÂ§ÑÁêÜ =========
    handleSubmit() {
      // 1. ÈááÈõÜË°®ÂçïÊï∞ÊçÆ
      const name = document.getElementById('name').value
      const phone = document.getElementById('phone').value
      const email = document.getElementById('email').value

      // ÊïôËÇ≤ÁªèÂéÜ
      const educationCards = document
        .getElementById('education-experience')
        .querySelectorAll('.card')
      const educationList = Array.from(educationCards).map((card) => {
        const inputs = card.querySelectorAll('input')
        return {
          school: inputs[0]?.value || '',
          time: inputs[1]?.value || '',
          major: inputs[2]?.value || '',
          degree: inputs[3]?.value || '',
          gpa: inputs[4]?.value || '',
          city: inputs[5]?.value || ''
        }
      })

      // Â∑•‰ΩúÁªèÂéÜ
      const workCards = document
        .getElementById('work-experience')
        .querySelectorAll('.card')
      const workList = Array.from(workCards).map((card) => {
        const inputs = card.querySelectorAll('input')
        return {
          company: inputs[0]?.value || '',
          time: inputs[1]?.value || '',
          title: inputs[2]?.value || '',
          city: inputs[3]?.value || ''
        }
      })

      // È°πÁõÆÁªèÂéÜ
      const projectCards = document
        .getElementById('project-experience')
        .querySelectorAll('.card')
      const projectList = Array.from(projectCards).map((card) => {
        const inputs = card.querySelectorAll('input')
        return {
          projectName: inputs[0]?.value || '',
          time: inputs[1]?.value || '',
          role: inputs[2]?.value || ''
        }
      })

      // 2. ÂÜôÂÖ• metadata_model
      // ---- 2.1 Â°´ÂÜô personalInfo ----
      metadataInstance.setContentForType('personalInfo', {
        name,
        phone,
        email
      })

      // ---- 2.2 Â°´ÂÜô education(educationList) ----
      educationList.forEach((edu) => {
        // ÂÅáËÆæ time ÊòØ "2016.09 - 2020.06" Ê†ºÂºè
        const [fromTime, toTime] = edu.time.split(' - ')
        const title = `${edu.school}`
        metadataInstance.setContentForType(
          'education',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            // ÂàùÂßãÂåñ‰∏Ä‰∏™Á©∫Êï∞ÁªÑÊàñÂç†‰ΩçÂÜÖÂÆπÔºåÂêéÁª≠ÂèØÁî± AI ËøõË°å‰∫ÆÁÇπÊÄªÁªì
            content: [
              // "Âú®Ê†°ÊúüÈó¥ÁßØÊûÅÂèÇ‰∏éÂêÑÁ±ªÁ§æÂõ¢Ê¥ªÂä®ÔºåÈîªÁÇº‰∫ÜÁªÑÁªáËÉΩÂäõ„ÄÇ",
              // "Ëé∑ÂæóÊ†°ÂÜÖÂ•ñÂ≠¶ÈáëÔºåË°®ÂΩ∞Â≠¶ÊúØÊàêÁª©‰ºòÁßÄ„ÄÇ",
              // "ÂèÇ‰∏éÂ§ö‰∏™ÁßëÁ†îÈ°πÁõÆÔºåÁßØÁ¥Ø‰∫Ü‰∏∞ÂØåÁöÑÂÆûË∑µÁªèÈ™å„ÄÇ",
            ]
          },
          title // ÂêåÊ≠•‰Ωú‰∏∫titleÁ¥¢Âºï
        )
      })

      // ---- 2.3 Â°´ÂÜô workExperience(workList) ----
      workList.forEach((work) => {
        const [fromTime, toTime] = work.time.split(' - ')
        const title = `${work.company}`
        metadataInstance.setContentForType(
          'workExperience',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            content: [
              // "Âú®È°πÁõÆ‰∏≠ÊàêÂäüÈ¢ÜÂØºÂõ¢ÈòüÔºåÊèêÂçá‰∫ÜÂ∑•‰ΩúÊïàÁéá„ÄÇ",
              // "ÈÄöËøá‰ºòÂåñÊµÅÁ®ãÔºåÂáèÂ∞ë‰∫Ü30%ÁöÑÈ°πÁõÆ‰∫§‰ªòÊó∂Èó¥„ÄÇ",
              // "ÊàêÂäüÂÆûÊñΩÊñ∞ÊäÄÊúØÔºåÊèêÂçá‰∫Ü‰∫ßÂìÅË¥®ÈáèÂíåÂÆ¢Êà∑Êª°ÊÑèÂ∫¶„ÄÇ",
            ]
          },
          title
        )
      })

      // ---- 2.4 Â°´ÂÜô projectExperience(projectList) ----
      projectList.forEach((proj) => {
        const [fromTime, toTime] = proj.time.split(' - ')
        const title = `${proj.projectName}`
        metadataInstance.setContentForType(
          'projectExperience',
          {
            title,
            from_time: fromTime ? fromTime.trim() : '',
            to_time: toTime ? toTime.trim() : '',
            content: [
              // "ÊàêÂäüÂÆåÊàêÈ°πÁõÆÔºåÊèêÂçá‰∫ÜÂõ¢ÈòüÂçè‰ΩúÊïàÁéá„ÄÇ",
              // "Âú®È°πÁõÆ‰∏≠ÂºïÂÖ•Êñ∞ÊäÄÊúØÔºåÊòæËëóÊèêÈ´ò‰∫ÜÂ∑•‰ΩúË¥®Èáè„ÄÇ",
              // "Ë¥üË¥£È°πÁõÆÁÆ°ÁêÜÔºåÁ°Æ‰øùÊåâÊó∂‰∫§‰ªòÂπ∂Êª°Ë∂≥ÂÆ¢Êà∑ÈúÄÊ±Ç„ÄÇ",
            ]
          },
          title
        )
      })

      // 3. Â∞ÜÂéüÂßãË°®ÂçïÊï∞ÊçÆÔºàÈùû metadata_model ÁªìÊûÑÔºâÁªßÁª≠Â≠òÂÖ• localStorageÔºåÊñπ‰æøÂêéÁª≠‰ΩøÁî®
      const formData = {
        name,
        phone,
        email,
        educationList,
        workList,
        projectList
      }
      localStorage.setItem('resumeFormData', JSON.stringify(formData))

      // 4. Ë∑≥ËΩ¨Âà∞ÂàõÂª∫ÁÆÄÂéÜÁöÑÈ°µÈù¢
      this.$router.push('/create-resume')
    },

    // ================== ÊïôËÇ≤ÁªèÂéÜ ==================
    addEducationExperience() {
      const container = document.getElementById('education-experience')
      let firstCard = container.querySelector('.card')
      if (firstCard) {
        const newCard = firstCard.cloneNode(true)
        this.clearInputs(newCard)
        container.appendChild(newCard)
      } else {
        const template = document.getElementById('education-card-template')
        const clone = template.cloneNode(true)
        this.clearInputs(clone)
        container.appendChild(clone)
      }
    },

    // ================== Â∑•‰ΩúÁªèÂéÜ ==================
    addWorkExperience() {
      const container = document.getElementById('work-experience')
      let firstCard = container.querySelector('.card')
      if (firstCard) {
        const newCard = firstCard.cloneNode(true)
        this.clearInputs(newCard)
        container.appendChild(newCard)
      } else {
        const template = document.getElementById('work-card-template')
        const clone = template.cloneNode(true)
        this.clearInputs(clone)
        container.appendChild(clone)
      }
    },

    // ================== È°πÁõÆÁªèÂéÜ ==================
    addProjectExperience() {
      const container = document.getElementById('project-experience')
      let firstCard = container.querySelector('.card')
      if (firstCard) {
        const newCard = firstCard.cloneNode(true)
        this.clearInputs(newCard)
        container.appendChild(newCard)
      } else {
        const template = document.getElementById('project-card-template')
        const clone = template.cloneNode(true)
        this.clearInputs(clone)
        container.appendChild(clone)
      }
    },

    // ================== Âà†Èô§Âç°Áâá ==================
    removeCard(event) {
      const btn = event.currentTarget
      const card = btn.closest('.card')
      if (card) {
        card.remove()
      }
    },

    clearInputs(cardElement) {
      const inputs = cardElement.querySelectorAll('input')
      inputs.forEach((inp) => (inp.value = ''))
    }
  }
}
</script>

