<template>
  <div class="app">
    <!-- Â∑¶‰æß -->
    <div class="left-container">
      <!-- Â¶ÇÊûúÂ∑≤ÈÄâ‰∏≠Êüê‰∏™titleÔºåÂ∞±ÊòæÁ§∫ChatComponent -->
      <template v-if="currentSelectedTitle != ''">
        <ChatComponent 
          :modules="chatModules"
          @update-resume="handleUpdateResume"
          :currentSelectedTitle="currentSelectedTitle"
          @close-chat="handleCloseChat"
        />
      </template>
      <!-- Âê¶ÂàôÔºåÊòæÁ§∫Êàë‰ª¨Ëá™ÂÆö‰πâÁöÑ‚ÄúÈÄâÊã©Ê®°Âùó‚ÄùÁªÑ‰ª∂ -->
      <template v-else>
        <SelectModuleComponent
          :chatModules="chatModules"
          @selected-module-changed="handleSelectedModuleChanged"
        />
      </template>
    </div>

    <!-- Âè≥‰æß -->
    <CVComponent 
      :highlightTitle="currentSelectedTitle" 
      @selected-module-changed="handleSelectedModuleChanged"
    />
  </div>
</template>

<script>
import ChatComponent from '@/components/ChatComponent.vue'
import CVComponent from '@/components/CVComponent.vue'
import SelectModuleComponent from '@/components/SelectModuleComponent.vue' // <-- Êñ∞Â¢ûÂºïÂÖ•
import metadataInstance from '@/models/metadata_model.js'

export default {
  name: 'CreateResume',
  components: {
    ChatComponent,
    CVComponent,
    SelectModuleComponent // <-- Ê≥®ÂÜå
  },
  data() {
    return {
      // ÂΩìÂâçÊ≠£Âú®ËÆ®ËÆ∫ÁöÑÊ†áÈ¢òÔºåÁî®‰∫éÂú® CV ‰∏≠È´ò‰∫Æ
      currentSelectedTitle: ''
    }
  },
  computed: {
    /**
     * Âä®ÊÄÅÁîüÊàêËÅäÂ§©Ê®°Âùó tabs
     * Ôºà‰æùÁÑ∂‰ªé metadata_model ‰∏≠ÂèñÊï∞ÊçÆÔºåËá™Âä®ÁîüÊàêÊïôËÇ≤/Â∑•‰Ωú/È°πÁõÆÁ≠âÊ†áÁ≠æÔºâ
     */
    chatModules() {
      const result = []

      // 1) ÈÅçÂéÜÊïôËÇ≤ÁªèÂéÜ
      const educationList = metadataInstance.contentForType('education') || []
      educationList.forEach((edu, index) => {
        const eduTitle = edu.title || `ÊïôËÇ≤ÁªèÂéÜ-${index + 1}`
        result.push({
          type: 'education',
          title: eduTitle
        })
      })

      // 2) ÈÅçÂéÜÂ∑•‰ΩúÁªèÂéÜ
      const workList = metadataInstance.contentForType('workExperience') || []
      workList.forEach((work, index) => {
        const workTitle = work.title || `Â∑•‰ΩúÁªèÂéÜ-${index + 1}`
        result.push({
          type: 'workExperience',
          title: workTitle
        })
      })

      // 3) ÈÅçÂéÜÈ°πÁõÆÁªèÂéÜ
      const projectList = metadataInstance.contentForType('projectExperience') || []
      projectList.forEach((proj, index) => {
        const projTitle = proj.title || `È°πÁõÆÁªèÂéÜ-${index + 1}`
        result.push({
          type: 'projectExperience',
          title: projTitle
        })
      })

      return result
    }
  },
  methods: {
    /**
     * ÂΩì ChatComponent ÂæóÂà∞ GPT ËøîÂõûÁöÑÁÆÄÂéÜ JSON Êó∂
     * ‰ºöÈÄöËøá update-resume ÂêëÁà∂ÁªÑ‰ª∂ÂèëÂ∞Ñ
     */
    handleUpdateResume(newMetaData) {
      console.log('handleUpdateResume newMetaData', newMetaData)
      // Ê†πÊçÆÊñ∞Êï∞ÊçÆÊõ¥Êñ∞ metadata Ê®°Âûã
      metadataInstance.setContentForTitle(newMetaData.resumeData.title, newMetaData.resumeData)
    },

    /**
     * Êé•Êî∂‰ªé ChatComponent ÂèëÂ∞ÑÁöÑ "selected-module-changed" ‰∫ã‰ª∂
     * Êõ¥Êñ∞ÂΩìÂâçÈÄâÊã©ÁöÑÊ†áÈ¢òÔºå‰ª•‰æøÂú® CV ‰∏≠È´ò‰∫Æ
     */
    handleSelectedModuleChanged(moduleItem) {
      
      this.currentSelectedTitle = moduleItem.title
    },

    /**
     * Êé•Êî∂‰ªé ChatComponent ÂèëÂ∞ÑÁöÑ "close-chat" ‰∫ã‰ª∂
     * ÂÖ≥Èó≠ÂΩìÂâçÊ≠£Âú®ËÆ®ËÆ∫ÁöÑÊ†áÈ¢ò
     */
    handleCloseChat() {
      this.currentSelectedTitle = ''
    } 
  }
}
</script>

<style scoped>
.app {
  display: flex;
  height: calc(100vh - 60px);
}
</style>
